:root {
  --font-body: Arial, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

body {
  background-color: black;
  color: #fff;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  #top #profile-mobile {
    width: 40vw;
    position: absolute;
    top: -3.2vh;
    right: -5.25vw;
  }

  #navbar {
    position: absolute;
    right: 0;
    top: 20px;
  }

  .title {
    position: absolute;
    left: 1.25vw;
    top: 0;
  }

  .intro, #about {
    width: 60%;
    z-index: 400;
    text-align: left;
    position: absolute;
    left: 3vw;
    top: 10vh;
  }

  #about {
    top: 19vh;
  }

  .contact-upper {
    position: absolute;
    left: 2vw;
    top: 31vh;
  }

  .experience {
    margin-top: 39vh;
    padding-bottom: 45vh;
  }

  .html, .css, .javascript, .react, .sass, .accessibility {
    position: absolute;
  }

  .html, .sass {
    left: 10vw;
    top: 37.5vh;
  }

  .css {
    top: 45vh;
  }

  .css, .react, .accessibility {
    left: 50vw;
  }

  .javascript, .react {
    top: 55vh;
  }

  .javascript {
    left: 11vw;
  }

  .sass {
    left: 8vw;
    top: 68vh;
  }

  .accessibility {
    top: 68vh;
  }

  #contact-middle {
    left: 66vw;
  }

  .card img {
    width: 45vw;
  }

  .card-col {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  .card-col .advice {
    position: relative;
    right: 26vw;
  }

  .clock {
    position: relative;
    left: 3vw;
  }

  .contact-form, .contact-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .contact-form p, .contact-info p {
    width: 45%;
  }

  form {
    width: 45%;
    margin-top: 2em;
  }

  .footer, #bottom {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: 2em;
  }
}
@media (min-width: 1024px) {
  #top #profile-mobile {
    width: 33vw;
  }
}
@media (min-width: 1440px) {
  #top #profile-mobile {
    width: 24vw;
  }

  .links {
    left: 70vw;
    width: 100%;
  }

  .fcc-markdown {
    left: 1vw;
  }

  .card-links {
    position: relative;
    bottom: 30vh;
    left: 20vw;
    display: flex;
    width: 25%;
    flex-direction: column;
    display: none;
  }

  .show {
    display: flex;
  }

  form .send-msg {
    width: 31%;
  }

  .html {
    left: 5vw;
  }

  .css {
    left: 28vw;
  }

  .javascript {
    left: 50vw;
    top: 45vh;
  }

  .sass {
    top: 65vh;
    left: 5vw;
  }

  .react {
    top: 65vh;
    left: 29vw;
  }

  .accessibility {
    top: 65vh;
  }

  hr:first-of-type {
    position: relative;
    top: 2.5em;
  }

  .header #navbar a img:hover, #bottom .footer-links a:hover {
    color: #4EE1A0;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

header {
  text-align: center;
  background-color: seagreen;
  color: #fff;
}
header h1 {
  font-size: 65px;
}
header .nav-link {
  color: #fff;
}
header .nav-link:hover {
  color: orangered;
}
header #active {
  border-bottom: 3px solid burlywood;
  padding-bottom: 0.45em;
}
header .navbar-list {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  list-style-type: none;
  font-size: 25px;
  padding-bottom: 0.9em;
}

h2 {
  font-size: 40px;
  text-align: center;
}
h2 span {
  border-bottom: 3px solid #4EE1A0;
  border-spacing: 30px;
}

.bio {
  font-size: 26px;
  text-align: center;
}

.contact-upper, .contact-middle {
  text-transform: uppercase;
  font-size: 26px;
  color: #fff;
  border-bottom: 3px solid #4EE1A0;
  border-spacing: 30px;
}

.skill, .project-title, .contact-title {
  font-size: 40px;
}

.years {
  font-size: 26px;
  color: #D9D9D9;
  position: relative;
  bottom: 2em;
}

.card-title {
  font-size: 24px;
  line-height: 32px;
}

.card-links a {
  text-transform: uppercase;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 2.29px;
  color: #fff;
  border-bottom: 3px solid #4EE1A0;
  border-spacing: 30px;
}

.card-links a:first-of-type {
  margin-right: 1.2em;
}

.contact-txt {
  color: #D9D9D9;
  font-size: 16px;
  line-height: 26px;
}

.footer-title-link {
  margin-top: 2em;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.33px;
  color: #fff;
}

.send-msg {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 26px;
  letter-spacing: 2.29px;
  background-color: transparent;
  color: #fff;
  border: none;
  border-bottom: 3px solid #4EE1A0;
  border-spacing: 30px;
  width: 50%;
  position: relative;
  left: 8em;
}

form input, form textarea {
  padding-block: 1rem;
  background: transparent;
  border: none;
  border-bottom: 1px solid #fff;
  width: 80%;
  margin-bottom: 1em;
}

.contact-title, .contact-txt {
  position: relative;
  left: 1em;
}

form {
  position: relative;
  left: 2.2em;
}

form input::placeholder, form textarea::placeholder {
  font-family: sans-serif;
  color: #D9D9D9;
  text-transform: uppercase;
  font-size: 1rem;
  line-height: 1.625rem;
}

form textarea {
  padding-bottom: 3.4rem;
}

form input::placeholder, form textarea[placeholder=Message]::placeholder {
  color: #D9D9D9;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#profile-mobile {
  margin: 2.5em;
  width: 115%;
}

.links img {
  margin-right: 1.2em;
}

.links {
  position: relative;
  z-index: 50;
}

.html, .css, .javascript, .accessibility, .react, .sass {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.html {
  margin-top: 5em;
}

.contact-middle {
  position: relative;
  bottom: 3em;
  left: 6.3em;
}

.card-img {
  width: 95vw;
}

.card {
  position: relative;
  right: 1em;
  margin-bottom: 1.5em;
}

.proj-skills {
  color: #D9D9D9;
  font-size: 18px;
  line-height: 28px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.meet-css, .elearn-css {
  position: relative;
  right: 11em;
}

.elearn-css {
  right: 2em;
}

.elearn-javascript {
  position: relative;
  right: 3.5em;
}

#proj-first-css {
  right: 3em;
}

.meet-javascript {
  position: relative;
  right: 6em;
}

.contact-form {
  margin-top: 4em;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2em;
}

.footer-links img {
  margin-right: 1.2em;
}

hr {
  margin-top: 3em;
}