@use '../util' as *;

.container {
	padding: 0 rem(24);
	max-width: rem(1110);
	margin: 0 auto;

	@include breakpoint(large) {
		padding: 0;
		transform: translate(0, 0);
	}
}

.header {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

#profile-mobile {
	margin: 2.5em;
	width: 115%;
}

.links img {
	margin-right: 1.2em;
}

.links{
	position: relative;
	z-index: 50;
}

.html,.css,.javascript,.accessibility,.react,.sass {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.html {
	margin-top: 5em;
}

.contact-middle {
	position: relative;
	bottom: 3em;
	left: 6.3em;
}

.card-img {
	width: 95vw;
}

.card {
	position: relative;
	right: 1em;
	margin-bottom: 1.5em;
}

.proj-skills {
	color: #D9D9D9;
	font-size: 18px;
	line-height: 28px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.meet-css,.elearn-css {
	position: relative;
	right: 11em;
}

.elearn-css {
	right: 2em;
}

.elearn-javascript  {
	position: relative;
	right: 3.5em;
}

#proj-first-css {
	right: 3em;
}

.meet-javascript {
	position: relative;
	right: 6em;
}

.contact-form {
	margin-top: 4em;
}

.footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-bottom: 2em;
}

.footer-links img {
	margin-right: 1.2em;
}

hr {
	margin-top: 3em;
}